.form {
    margin-top: 1rem;
    display: flex;
    align-items: center;
    justify-content: space-between;
    flex-direction: column;
    position: relative;
}
.form::before {
    width: 100%;
    content: "";
    position: absolute;
    top: -10px;
    border-bottom: 2px solid #e5e5e5;
}
input,
textarea {
    font-family: "Montserrat";
    min-height: 15px;
    outline: none;
    border: 1px solid #cacaca;
    box-shadow: 2px 4px 6px rgba(79, 79, 79, 0.15);
    border-radius: 2px;
    resize: none;
    width: 100%;
    min-width: 500px;
    padding: 0.5rem;
}
.heading {
    margin-bottom: 10px;
}
.save {
    width: 200px;
    background: #f0f7ff;
    color: #1877f2;
    border: 1px solid #cccccc;
    box-shadow: 2px 4px 6px rgba(79, 79, 79, 0.15);
    border-radius: 15px;
    font-family: "Gilroy-Medium";
    font-size: 15px;
    line-height: 18px;
    padding: 8px 0 8px 0;
}
.save:hover {
    cursor: pointer;
    transition: all 0.3s;
    color: #fcfcfc;
    background: #0e58ac;
}
